:root {
  --c__main: #ffffffe6;
  --c__accent: #1cacff;
  --bgc__main: #000000e6;
  --bgc__secondary: #282828;
  --items: 1;
  --gap: 20px;
  --trs__main: 1s ease-in-out 0s;
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

ol, ul {
  list-style: none;
}

a {
  text-decoration: none;
}

:active, :focus {
  outline: 0;
}

.hero__bg-img-list, .movies__list, .pagination {
  justify-content: center;
  gap: var(--gap);
  flex-wrap: wrap;
  display: flex;
}

.movie-card {
  flex-basis: calc((100% - var(--gap) * (var(--items)  - 1)) / var(--items));
}

@media (min-width: 768px) {
  .movie-card {
    --items: 2;
  }
}

@media (min-width: 1200px) {
  .movie-card {
    --items: 3;
  }
}

.modal__close-btn, .section.hero .container, .section.hero, .search-form, header, .backdrop, .mobile-menu__btn, .btn, .paginationBtn, .paginationBtn__extra, .btn-up, .arrow__up {
  align-items: center;
  display: flex;
}

.section.hero .container, .section.hero, .search-form, header, .backdrop, .mobile-menu__btn, .btn, .paginationBtn, .paginationBtn__extra, .btn-up, .arrow__up {
  justify-content: center;
}

.movie-card, .mobile-menu {
  flex-direction: column;
  display: flex;
}

.movie-details__btns, .movie-card__details-btn, .movie-card__img, .search-form__button, .modal__close-btn, .mobile-menu__btn, .btn, .paginationBtn, .paginationBtn__extra, .btn-up {
  cursor: pointer;
}

.movie-details__video iframe, .movie-details__video, .movie-details__btns, .movie-details__desc-info span, .movie-details__img, .movie-details, .movie-card__desc, .movie-card__img, .movie-card, .search-form__input, .search-form__button, .search-form, .modal__close-btn .burger-line, .burger-line, .btn, .paginationBtn, .paginationBtn__extra, .btn-up, .decor-line {
  border-radius: 8px;
}

.movie-details__video, .movie-card__details-btn, .modal__close-btn .burger-line:first-child, .modal__close-btn, .burger-line, .decor-line, .visually-hidden {
  position: absolute;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  overflow: hidden;
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  letter-spacing: .03em;
  color: var(--c__main);
  background-color: var(--bgc__main);
  font-size: 16px;
  line-height: 1.2;
  position: relative;
}

body.isLoading, body.modalIsOpen, body.mobileMenuIsOpen {
  overflow: hidden;
}

iframe, img {
  max-width: 100%;
  height: auto;
  display: block;
}

button, a {
  color: inherit;
}

button[disabled] {
  cursor: auto;
}

svg {
  display: block;
}

.section {
  padding: 32px 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

@media (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.title {
  text-align: center;
  font-size: 28px;
  line-height: 1;
}

.subtitle {
  font-size: 20px;
  line-height: 1;
}

.header__logo {
  font-size: 24px;
  font-weight: 600;
}

.semipolar-spinner, .semipolar-spinner * {
  box-sizing: border-box;
}

.semipolar-spinner {
  height: 65px;
  width: 65px;
  position: relative;
}

.semipolar-spinner .ring {
  border: 3.25px solid #0000;
  border-top-color: var(--c__accent);
  border-left-color: #ffaf23;
  border-radius: 50%;
  animation: 2s infinite semipolar-spinner-animation;
  position: absolute;
}

.semipolar-spinner .ring:nth-child(1) {
  height: 65px;
  width: 65px;
  z-index: 5;
  animation-delay: .8s;
  top: 0;
  left: 0;
}

.semipolar-spinner .ring:nth-child(2) {
  height: 52px;
  width: 52px;
  z-index: 4;
  animation-delay: .6s;
  top: 6.5px;
  left: 6.5px;
}

.semipolar-spinner .ring:nth-child(3) {
  height: 39px;
  width: 39px;
  z-index: 3;
  animation-delay: .4s;
  top: 13px;
  left: 13px;
}

.semipolar-spinner .ring:nth-child(4) {
  height: 26px;
  width: 26px;
  z-index: 2;
  animation-delay: .2s;
  top: 19.5px;
  left: 19.5px;
}

.semipolar-spinner .ring:nth-child(5) {
  height: 13px;
  width: 13px;
  z-index: 1;
  animation-delay: 0s;
  top: 26px;
  left: 26px;
}

@keyframes semipolar-spinner-animation {
  50% {
    transform: rotate(360deg)scale(.7);
  }
}

.arrow__up {
  width: 32px;
  height: 32px;
  position: relative;
}

.decor-line {
  width: 20px;
  height: 3px;
  background-color: #e6e6e6;
  margin: 0;
}

.decor-line:first-child {
  transform: rotate(-45deg)translateX(-4px)translateY(-5px);
}

.decor-line:last-child {
  transform: rotate(45deg)translateX(4px)translateY(-5px);
}

.btn, .paginationBtn, .paginationBtn__extra, .btn-up {
  font-size: inherit;
  background-color: var(--bgc__secondary);
}

.btn.current, .current.paginationBtn, .current.paginationBtn__extra, .current.btn-up {
  background-color: var(--c__accent);
}

.mobile-menu__btn {
  width: 32px;
  height: 60px;
  background: none;
  margin-left: auto;
  position: relative;
}

.mobile-menu__btn.isOpen {
  justify-content: flex-start;
}

.header.isScrolling .mobile-menu__btn {
  height: 40px;
}

@media (min-width: 768px) {
  .mobile-menu__btn {
    display: none;
  }
}

.burger-line {
  width: 100%;
  height: 3px;
  background-color: currentColor;
  margin: 0;
  transition-property: height, opacity, transform;
  transition-duration: .4s;
}

.burger-line:first-child {
  transform: translateY(8px);
}

.burger-line:last-child {
  transform: translateY(-8px);
}

.mobile-menu__btn.isOpen .burger-line:first-child {
  transform: rotate(-45deg);
}

.mobile-menu__btn.isOpen .burger-line:nth-child(2n) {
  opacity: 0;
}

.mobile-menu__btn.isOpen .burger-line:last-child {
  transform: rotate(45deg);
}

.btn, .paginationBtn, .paginationBtn__extra, .btn-up {
  font-size: inherit;
  background-color: var(--bgc__secondary);
}

.btn.current, .current.paginationBtn, .current.paginationBtn__extra, .current.btn-up {
  background-color: var(--c__accent);
}

.btn-up {
  width: 48px;
  height: 48px;
  background-color: var(--bgc__main);
  transition: opacity var(--trs__main);
  border-radius: 50%;
  position: fixed;
  bottom: 32px;
  right: 32px;
}

.btn-up.isHidden {
  opacity: 0;
  pointer-events: none;
}

.pagination {
  --gap: 8px;
  z-index: 1;
  align-items: center;
  padding: 16px;
}

.paginationBtn, .paginationBtn__extra {
  width: 40px;
  height: 40px;
}

.paginationBtn.isHidden, .isHidden.paginationBtn__extra {
  display: none;
}

.paginationBtn__extra--arrov div {
  pointer-events: none;
  transform: rotate(-90deg);
}

.paginationBtn__extra--arrov:last-child div {
  transform: rotate(90deg);
}

@media (max-width: 767px) {
  .paginationBtn:nth-child(-n+2), .paginationBtn__extra:nth-child(-n+2), .paginationBtn:nth-last-child(-n+3), .paginationBtn__extra:nth-last-child(-n+3) {
    flex: 1 0 18%;
    order: -1;
    display: flex;
  }

  .paginationBtn:nth-child(1).isHidden, .paginationBtn__extra:nth-child(1).isHidden, .paginationBtn:nth-last-child(1).isHidden, .paginationBtn__extra:nth-last-child(1).isHidden {
    color: #f5deb333;
    pointer-events: none;
  }

  .paginationBtn:nth-child(3), .paginationBtn__extra:nth-child(3) {
    display: none;
  }
}

@media (max-width: 400px) {
  .paginationBtn:nth-child(-n+2), .paginationBtn__extra:nth-child(-n+2), .paginationBtn:nth-last-child(-n+3), .paginationBtn__extra:nth-last-child(-n+3) {
    flex: 1 0 17%;
  }
}

.backdrop {
  z-index: 3000;
  background-color: #000000e6;
  position: fixed;
  inset: 0;
}

.backdrop.isHidden {
  display: none;
}

.backdrop.mod {
  z-index: 2000;
}

@media (orientation: landscape) {
  .backdrop.mod {
    align-items: flex-start;
    overflow: scroll;
  }
}

.modal.container {
  position: relative;
}

.modal__close-btn {
  width: 32px;
  height: 32px;
  background-color: #0000;
  justify-content: center;
  top: 4px;
  right: 16px;
}

@media (min-width: 768px) and (max-width: 800px) {
  .modal__close-btn {
    right: 32px;
  }
}

.modal__close-btn .burger-line {
  width: 100%;
  height: 3px;
  background-color: #fff;
  margin: 0;
}

.modal__close-btn .burger-line:first-child {
  transform: rotate(-45deg);
}

.modal__close-btn .burger-line:last-child {
  transform: rotate(45deg);
}

.mobile-menu {
  color: #fff;
  z-index: 1001;
  visibility: hidden;
  pointer-events: none;
  transition: transform var(--transition__main), visibility var(--transition__main);
  background-color: #000000e6;
  padding: 28px 16px;
  position: fixed;
  inset: 60px 0 0;
  overflow-y: scroll;
  transform: translateX(100%);
}

body.isScrolling .mobile-menu {
  top: 44px;
}

.mobile-menu.isOpen {
  visibility: visible;
  pointer-events: all;
  transform: translateX(0);
}

@media (min-width: 768px) {
  .mobile-menu {
    left: 50%;
  }
}

@media (min-width: 1200px) {
  .mobile-menu {
    display: none;
  }
}

header {
  width: 100%;
  height: 40px;
  z-index: 10;
  background-image: linear-gradient(#000000e6, #0000001a);
  position: absolute;
  top: 0;
  left: 0;
}

.search-form {
