  padding-top: 12px;
}

.search-form__button {
  color: #fff;
  background-color: #000c;
  border: 1px solid #ffffff1a;
  padding: 8px;
}

.search-form__input {
  min-width: 100px;
  background-color: rgbargba(255, 255, 255, .8);
  padding: 8px;
}

.movie-card {
  --items: 2;
  color: var(--c__main);
  background-color: var(--bgc__secondary);
