    --items: 5;
  }
}

.movie-card__img {
  width: 100%;
  aspect-ratio: 2 / 3;
  object-fit: cover;
}

.movie-card__desc {
  flex-direction: column;
  flex-grow: 1;
  gap: 12px;
  padding: 12px;
  font-size: 12px;
  display: flex;
}

.movie-card__title, .movie-card__year {
  font-size: 16px;
}

.movie-card__ganres {
  margin-top: auto;
}

.movie-card__details-btn {
  background-color: #0000;
  inset: 0;
}

.movie-details {
  background-color: var(--bgc__main);
  gap: 16px;
  padding: 16px;
  display: flex;
  position: relative;
