  }
}

@media (min-width: 1200px) {
  .movie-details__desc {
    font-size: 24px;
  }
}

.movie-details__desc-list {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.movie-details__title {
  font-size: 16px;
}

@media (min-width: 768px) {
  .movie-details__title {
