}

@media (min-width: 1200px) {
  .movie-details__video {
    padding: 32px;
  }
}

.movie-details__video iframe {
  width: 100%;
  aspect-ratio: 16 / 9;
}

footer {
  background-color: var(--bgc__secondary);
  padding: 16px;
}

.section.hero {
  height: 360px;
  background: #000;
  padding: 0;
  position: relative;
  overflow: hidden;
}

@media (min-width: 500px) {
  .section.hero {
    height: 360px;
  }
}

@media (min-width: 768px) {
  .section.hero {
    height: 400px;
  }
}

@media (min-width: 1200px) {
  .section.hero {
    height: 500px;
  }
}

.section.hero .container {
  z-index: 1;
  flex-direction: column;
